<template>
    <v-container class="mt-5 pt-5 animate-fade-in">
    <v-layout align-center column>
        <v-flex>
            <h1 class="text-xs-center mt-5 pt-5 primary--text">In a busy world we live in, we all need reminders.</h1>
        </v-flex>
        <v-flex class="mt-4">
             <v-btn  class="primary text-capitalize" @click="registerDialog = true">Log In</v-btn>
            <v-btn class="primary text-capitalize" @click="loginDialog = true">Sign Up</v-btn>
        </v-flex>
        <v-flex class="mt-4">
          <img width="450" src="./../../public/note.png"/>
        </v-flex>
    </v-layout>
        <v-dialog
      v-model="loginDialog"
      max-width="500"
    >
      <v-card>
        <v-card-title class="headline pb-2 primary--text">New here?</v-card-title>
        <v-form
            @submit.prevent="signUp()"
            class="px-4 pb-4"
            ref="formReg"
            v-model="validReg"
            lazy-validation
        >
            <v-text-field
            v-model="firstName"
            :rules="firstNameRules"
            label="First Name"
            required
            ></v-text-field>

            <v-text-field
            v-model="lastName"
            :rules="lastNameRules"
            label="Last Name"
            required
            ></v-text-field>

            <v-text-field
            v-model="email"
            :rules="emailRules"
            label="E-mail"
            required
            ></v-text-field>

            <v-text-field
            label="Password"
            v-model="password"
            required
            :rules="passwordRules"
            :type="'password'"
            ></v-text-field>

            <v-text-field
            label="Re-type Password"
            v-model="repeatPassword"
            :error-messages='passwordMatchError()'
            required
            :type="'password'"
            ></v-text-field>

            <p v-if="error" class="red--text"> {{ error }} </p>
            <v-btn
            type="submit"
            @click="closeDialog"
            :disabled="!validReg"
            class="white--text text-capitalize mt-4 primary"
            >
            Create a new account
            </v-btn>
        </v-form>
      </v-card>
    </v-dialog>

      <v-dialog
      v-model="registerDialog"
      max-width="500"
    >
      <v-card>
        <v-card-title class="headline pb-2 primary--text">Already have an account?</v-card-title>
         <v-form
            @submit.prevent="logIn()"
            class="px-4 pb-4"
            ref="formLog"
            v-model="validLog"
            lazy-validation
        >

            <v-text-field
            v-model="emailLog"
            :rules="emailRulesLog"
            label="E-mail"
            required
            ></v-text-field>

            <v-text-field
            label="Password"
            v-model="passwordLog"
            :rules="passwordRulesLog"
            required
            :type="'password'"
            ></v-text-field>
            <p v-if="errorLog" class="red--text"> {{ errorLog }} </p>
            <v-btn
            :disabled="!validLog"
            class="white--text text-capitalize mt-4 primary"
            type="submit"
            >
            Log In
            </v-btn>
        </v-form>
      </v-card>
    </v-dialog>
    </v-container>
</template>

<style lang="sass">
body *
    font-family: 'Sniglet', cursive !important

.animate-fade-in
    animation: anim-fade-in .5s ease-in-out forwards


@keyframes anim-fade-in
    from
        opacity: 0
        transform: scale(0)

    to
        opacity: 1
        transform: scale(1)
</style>


<script>
import db from "@/firebase/init"
import firebase from "firebase"
import './../../node_modules/nprogress/nprogress.css'
import NProgress from 'nprogress'

  export default {
    data: () => ({
      loginDialog: null,
      registerDialog: null,

      validReg: true,
      firstName: '',
      firstNameRules: [
        v => !!v || 'First name is required',
        v => (v && v.length <= 20) || 'Name must be less than 20 characters'
      ],
      lastName: '',
      lastNameRules: [
        v => !!v || 'Last name is required',
        v => (v && v.length <= 20) || 'Name must be less than 20 characters'
      ],
      email: '',
      emailRules: [
        v => !!v || 'E-mail is required',
        v => /.+@.+/.test(v) || 'E-mail must be valid'
      ],
      password: '',
      repeatPassword: '',
      passwordRules: [
        v => !!v || 'Password is required',
        v => (v && v.length >= 6) || 'Password must be longer than 6 characters'
      ],
      error: null,

      validLog: true,
      emailLog: '',
      emailRulesLog: [
        v => !!v || 'E-mail is required',
        v => /.+@.+/.test(v) || 'E-mail must be valid'
      ],
      passwordRulesLog: [
        v => !!v || 'Password is required'
      ],
      passwordLog: '',
      errorLog: null
    }),

    methods: {
      signUp () {
        NProgress.start()
        if (this.$refs.formReg.validate()) {
            this.validReg = false
            this.$store.state.currentTheme = 'white'
            firebase.auth().createUserWithEmailAndPassword(this.email, this.password)
            .then(cred => {
              this.$router.push('/mynotes')
              NProgress.done()
              let user_id = cred.user.uid
              this.registerDialog = false
              db.collection('users').doc(cred.user.uid).set({
                id: user_id,
                firstName: this.firstName,
                lastName: this.lastName,
                currentTheme: 'white'
              }).catch(err => {
                NProgress.done()
                this.validReg = true
                this.error = err
              });
            })
        }
      },

      logIn() {
        if (this.$refs.formLog.validate()) {
            NProgress.start()
            this.validLog = false
            firebase.auth().signInWithEmailAndPassword(this.emailLog, this.passwordLog).then(() => {
              NProgress.done()
              this.errorLog = null
              this.loginDialog = false
              this.$router.push('/mynotes')
            }).catch(err => {
              this.errorLog = err
              this.validLog = true
            })
            setTimeout(() => {
              NProgress.done()
            }, 5000)
        }
      },

      passwordMatchError () {
          return (this.password === this.repeatPassword) ? '' : 'Passwords do not match'
      }
    }
  }
</script>